<template>
  <div>
    <v-card max-width="1000px" class="mx-auto mt-5">
      <v-data-table
        :headers="headers"
        :items="items"
        :hide-default-footer="true"
        disable-pagination
        disable-filtering
        disable-sort
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>Professores</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                  :disabled="locked"
                >
                  Novo professor
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-form @submit.prevent ref="form">
                      <v-row>
                        <v-col cols="6" sm="6">
                          <v-text-field
                            v-model="editedItem.name"
                            label="Nome do professor"
                            :rules="[rules.required]"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="6">
                          <v-text-field
                            number
                            v-model="editedItem.limite_dias_trabalho"
                            label="Número máximo de dias trabalhados"
                            :rules="[
                              rules.greaterThanZero(false),
                              rules.lessThanOrEqual(numeroDias, false),
                            ]"
                            clearable
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-form>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    Cancelar
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save"> OK </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="text-h5"
                  >Tem certeza que deseja excluir esse professor?
                </v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >Cancelar</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >OK</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <!-- eslint-disable-next-line -->
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)" :disabled="locked">
            mdi-pencil
          </v-icon>
          <v-icon small @click="deleteItem(item)" :disabled="locked">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
import validationMixin from "@/mixins/validation";

export default {
  name: "TabelaProfessores",
  props: ["items", "numeroDias", "locked"],
  mixins: [validationMixin],
  data() {
    return {
      headers: [
        { text: "Nome", value: "name" },
        { text: "Limite de dias de trabalho", value: "limite_dias_trabalho" },
        { text: "Ações", value: "actions", sortable: false, width: "10%" },
      ],
      editedIndex: -1,
      editedItem: {
        name: "",
      },
      defaultItem: {
        name: "",
      },
      dialogDelete: false,
      dialog: false,
      formTitle: null,
    };
  },

  watch: {
    dialog(val) {
      val || this.close();

      if (val === true) {
        this.resetValidation();
      }
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  methods: {
    editItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$emit("delete", this.editedIndex);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (!this.validate()) {
        return;
      }
      this.$emit("save", this.editedIndex, this.editedItem);
      this.close();
    },
  },
};
</script>

